window.onload = function () {
    var Line = /** @class */ (function () {
        function Line(icon, pfx, message) {
            this.icon = icon;
            this.pfx = pfx;
            this.message = message;
        }
        Line.prototype.type = function () {
            function restore(e, i) {
                if (e) {
                    var sValue = e.getAttribute('data-content');
                    if (i < sValue.length) {
                        e.innerHTML += sValue[i];
                        setTimeout(function () { restore(e, i); }, 100);
                        i++;
                    }
                }
            }
            restore(this.icon, 0);
            restore(this.pfx, 0);
            restore(this.message, 0);
        };
        return Line;
    }());
    function sleep(ms) {
        return new Promise(function (resolve) { return setTimeout(resolve, ms); });
    }
    function dispChars(eLine) {
        // Get HTML items
        var promptLine = new Line(eLine.getElementsByClassName('js-prompt-icon')[0], eLine.getElementsByClassName('js-prompt-prefix')[0], eLine.getElementsByClassName('js-prompt-text')[0]);
        eLine.classList.remove('hidden');
        promptLine.type();
    }
    function displayLines(eLines) {
        for (var i = 0; i < eLines.length; i++) {
            dispChars(eLines[i]);
        }
    }
    var eLines = document.getElementsByClassName('js-typewriting');
    displayLines(eLines);
};
